@import url(https://fonts.googleapis.com/css?family=Charm);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-transition: all .3s;
  transition: all .3s;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

* {
  margin: 0;
  padding: 0;
}

*, *::before, *::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}
#quote-box {
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: #fff;
    width: 65rem;
    padding: 2.5rem;
    border-radius: 3px;
}

@media screen and (max-width: 700px) {
    #quote-box {
        width: 40rem;
        padding: 2rem;
    }
    #text {
        font-size: 2.4rem;
    }
}

@media screen and (max-width: 450px) {
    #quote-box {
        width: 30rem;
        padding: 1.5rem;
        height: auto;
    }
    #text {
        font-size: 2.4rem;
    }
}

#text {
    font-size: 2.6rem;
    text-align: center;
    margin-bottom: 1rem;
    font-family: 'Charm', cursive;
}

#author {
    font-size: 1.8rem;
    text-align: right;
    margin-bottom: 1.5rem;
    font-style: italic;
}

.buttons__social {
    margin-right: 1rem;
    padding: 1.5rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    color: white;
    font-size: 1.8rem;
}

#new-quote {
    font-size: 1.6rem;
    color: white;
    cursor: pointer;
}

#new-quote {
    font-size: 1.8rem;
    border: none;
    padding: 1rem;
    border-radius: 5px;
    margin-left: auto;
}

.buttons {
    display: flex;
}
.creator {
    margin: 0;
    position: absolute;
    top: 80%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-size: 1.6rem;
    color: #dddddd;
}

@media only screen and (max-width: 600px) {
    .creator {
        top: 85%;
    }
}
